import React, {Component} from 'react';
import axios from 'axios';
import {Link} from "react-router-dom";
import NumberFormat from 'react-number-format';
import '../Rightcontent/ProductGrid.css';

class ProductGrid extends Component {
    constructor(props){
        super(props);
        this.state = {
            data : [],
            load : false
        }
        this.loadHandler = this.loadHandler.bind(this);
        this.loadHandler();
    }

    componentDidUpdate(prevProps){
        if(prevProps.classes !== this.props.classes){
            this.loadHandler();
        }
    }

    loadHandler = () => {
        if(this.props.type === "brands"){
            var link = 'http://13.229.249.101/dashboard/api/userapi/product/list/webnew/bybrand/'+this.props.classes;
        }
        else if(this.props.type === "class"){
            var link = 'http://13.229.249.101/dashboard/api/userapi/product/list/webnew/byclass/'+this.props.classes;
        }
        axios.get(link)
        .then(response => {
            console.log(response);
            this.setState({"data" : response.data});
        })
        .catch((error) => {
            console.log('error ' + error);
        });
    }

    render(){
        return (
            <div className="col-md-9 right-content">
                <div className="row">
                    <div className="col-12 mb-4">
                        <div className="row">
                        {
                            this.state.data.map((data, i)=>{
                                console.log(data);
                                if(data.imageWeb !== undefined){
                                    var image = data.imageWeb[0];
                                }
                                else{
                                    var image = "";
                                }
                            return (
                            <div className="col-md-4 col-sm-6 p-list mb-3" key={i}>
                                <a href={"#/details-product/"+data.categoryId}>
                                    <div className="item-top">
                                        <img src={ 'http://13.229.249.101/dashboard/api/web/uploads/category/'+image } width="300"></img>
                                    </div>
                                    <div className="item-bottom">
                                        <h5>{data.categoryName}</h5>
                                        <p className="price"><NumberFormat value={data.price1} displayType={'text'} thousandSeparator={'.'} decimalSeparator={','} prefix={'IDR'} /></p>
                                    </div>
                                </a>
                            </div>
                            )
                            })
                        }
                            
                        </div>
                    </div>
                </div>
            </div>
        )
    }
}

export default ProductGrid;