import React,{Component} from 'react';
import axios from 'axios';
import NumberFormat from 'react-number-format';

class ProductGrid extends Component{
    constructor(props){
        super(props);
        this.state = {
            data : []
        }
        this.loadHandler = this.loadHandler.bind(this);
        this.loadHandler();
    }

    loadHandler = () => {
        axios.get('http://13.229.249.101/dashboard/api/userapi/product/list/webnew/bycategory')
        .then(response => {
            this.setState({"data" : response.data});
        })
        .catch((error) => {
            console.log('error ' + error);
        });
    }

    render (){
        return(
            <div className="container">
                <div className="row">
                {
                    this.state.data.slice(0,8).map((data, i)=>{
                    if(data.imageWeb !== undefined){
                        var image = data.imageWeb[0];
                    }
                    else{
                        var image = "";
                    }
                    
                    return (
                    <div className="col-md-3 col-sm-6 p-list mb-3" key={i}>
                        <a href="#">
                            <div className="item-top">
                                <img src={ 'http://13.229.249.101/dashboard/api/web/uploads/category/'+image} width="100%"></img>
                            </div>
                            <div className="item-bottom">
                                <h5>{data.categoryName}</h5>
                                <p className="price"><NumberFormat value={data.price1} displayType={'text'} thousandSeparator={'.'} decimalSeparator={','} prefix={'IDR '} /></p>
                            </div>
                        </a>
                    </div>
                    )
                    })
                }
                </div>
            </div>
        );
    }
}

export default ProductGrid;