import React, {Component} from 'react';
import axios from 'axios';
import {Link} from "react-router-dom";
import {reactLocalStorage} from 'reactjs-localstorage';
import './Rightcontent.css';

class BalanceHistory extends Component{
    constructor(props){
        super(props);
        this.state = {
            page : 1,
            balanceHistory : [],
            isLoading:''
        }
        this.timer = null;
        this.loadBalance() 
    }

    componentDidUpdate (prevProps, prevState) {
        if(prevState.page !== this.state.page) {
          this.handleCheck();
        }
    }

    loadBalance = () => {
        var token= reactLocalStorage.get('auth');
        const AuthStr = "Bearer ".concat(token); 
        axios.get('http://13.229.249.101/dashboard/api/userapi/deposit/view/page/'+this.state.page , { headers: { Authorization: AuthStr } })
        .then((response) => {
            this.setState({"balanceHistory" : response.data});
        })
        .catch((error) => {
            document.location.href="";
            this.setState({error: 'Periksa Kembali Data Pada From Edit Account'})
            console.log(error);
        });
    }

    handleCheck = () => {
        // Clears running timer and starts a new one each time the user types
        clearTimeout(this.timer);
        this.timer = setTimeout(() => {
           this.loadBalance()   
        }, 1000);
    }

    changePageHanlder = (e) => {
        let change = {}
        change[e.target.name] = e.target.value
        this.setState(change)
    }

    nextPageHandler = () => {
        var pages = this.state.page + 1;
        this.setState({
            "page" : pages
        })
    }

    prevPageHandler = (pages) => {
        var pages = this.state.page - 1;
        if(pages > 0){
            this.setState({
                "page" : pages
            })
        }
    }
    updatePageHandler = (pages) => {
        this.setState({
            "page" : pages.target.textContent
        })
    }

    render(){
        return(
            <div className="col-md-9 mb-5 right-content-edit">
                <div className="row">
                        <div className="col-12 text-center">
                            <h2 className="text-left mb-2 border-0">Balance History</h2>
                        </div>
                </div>
                <table className="table mb-5">
                    <thead>
                        <tr>
                            <th>#</th>
                            <th>Description</th>
                            <th>Operation</th>
                            <th>Amount</th>
                            <th>Total</th>
                            <th>Time</th>
                        </tr>
                    </thead>
                    <tbody>
                    {
                        this.state.balanceHistory.map((balance, i) => {
                            return (
                                <tr key={i}>
                                    <td>{balance.id}</td>
                                    <td>{balance.description}</td>
                                    <td>{balance.operation}</td>
                                    <td>{balance.amount}</td>
                                    <td>{balance.total}</td>
                                    <td>{balance.time}</td>
                                </tr>
                            )
                        })
                    }
                    </tbody>
                </table>
                <nav aria-label="Page navigation example">
                    <ul className="pagination float-right">
                        <li className="page-item"><Link className="page-link" onClick={this.prevPageHandler}><i className="fa fa-chevron-left"></i> Previous</Link></li>
                        {this.state.page - 1 >0 ? <li className="page-item"><Link onClick={this.updatePageHandler} className="page-link">{parseInt(this.state.page) - 1}</Link></li>:null}
                        <li className="page-item"><Link onClick={this.updatePageHandler} className="page-link">{this.state.page}</Link></li>
                        <li className="page-item"><Link onClick={this.updatePageHandler.bind(this.target)} className="page-link">{parseInt(this.state.page) + 1}</Link></li>
                        <li className="page-item"><Link className="page-link" onClick={this.nextPageHandler}>Next <i className="fa fa-chevron-right"></i></Link></li>
                    </ul>
                </nav>
            </div>
        );
    }
}

export default BalanceHistory;