import React,{ Component } from 'react';
import axios from 'axios';
import $ from 'jquery';
import '../../../assets/css/jquery.bxslider.css';
import '../../../assets/js/main';
import ProductDescription from './ProductDescription';
class ProductView extends Component {
    constructor(props) {
        super(props);
        this.state = {
            product : [],
            link    : "http://13.229.249.101/dashboard/api/web/uploads/category/",
            image   : [],
            idcategory : ""
        } 
        this.getProductHandler = this.getProductHandler.bind(this);
        this.getProductHandler(this.props.idCategory);
    }
    componentDidMount(){
        
    }
    componentDidUpdate(prevProps){
        if(this.props.idCategory !== prevProps.idCategory){
            this.getProductHandler(this.props.idCategory);
            this.setState({"idcategory" : this.props.idCategory});
        }   
    }

    getProductHandler = (idCategory) => {
        var headers = {
            'Content-Type': 'application/json'
        }
        axios.get("http://13.229.249.101/dashboard/api/userapi/product/detail/webnew/category/"+idCategory, {headers: headers})
        .then((response) => {
            this.setState({'product' : response.data});
            if(response.data.imageWeb !== undefined && response.data.imageWeb !== ''){
            this.setState({'image' : response.data.imageWeb});  
            }
            $(".fancybox").fancybox();
            $('.bxslider').bxSlider({
                nextSelector : '#slider-next',
                prevSelector : '#slider-prev',
                prevText : '<i class="fa fa-chevron-circle-left"></i>',
                nextText : '<i class="fa fa-chevron-circle-right"></i>',
                touchEnabled : false,
                infiniteLoop: false
            });
            
        })
    }

    render(){     
        return(
            <div className="row product-details">
                <div className="product-left-column col-lg-4 col-md-4 col-sm-5">
                    <div className="product-s-l">
                        <div className="product-large">
                            <div className="tab-content">
                                {
                                    this.state.image.map((image,index) => {
                                        if(index === 0){
                                            var active = "active in";
                                        }
                                        else{
                                            var active = "in";
                                        }
                                        return (
                                            <div key={index} className={"tab-pane "+active} id={"img-"+index}>
                                                <a className="fancybox" data-fancybox-group="group" href={ 'http://13.229.249.101/dashboard/api/web/uploads/category/'+image }>
                                                    <img alt="" src={ 'http://13.229.249.101/dashboard/api/web/uploads/category/'+image }/>
                                                </a>
                                            </div>
                                        )
                                    })
                                }
                            </div>
                        </div>
                        <div className="product-small">
                            <ul className="nav nav-tabs bxslider" role="tablist">
                            {
                                this.state.image.map((image,index) => {
                                    if(index === 0){
                                        var activer = "active";
                                    }
                                    else{
                                        var activer = "";
                                    }
                                    return (
                                        <li key={index} className={activer}>
                                            <a href="javascript:void(0)" data-href={"#img-"+index } data-toggle="tab">
                                                <img alt="" src={'http://13.229.249.101/dashboard/api/web/uploads/category/'+image}/>
                                            </a>
                                        </li>
                                    );
                                })
                            }
                            </ul>
                            {/* <!-- BX Slider Navigation --> */}
                            <div className="outside">
                                <p><span id="slider-prev"></span> <span id="slider-next"></span></p>
                            </div>  
                            {/* <!-- BX Slider Navigation --> */}
                        </div>
                    </div>
                </div>
                <ProductDescription tag={this.state.product.tag} descriptionweb={this.state.product.descriptionWeb} name={this.state.product.name} mill={this.state.product.mill} ></ProductDescription>
            </div>
        );
    }
}
export default ProductView;