import React,{Component} from 'react';
import axios from 'axios';
import $ from 'jquery';

class TopSlide extends Component{
    constructor(props){
        super(props);
        this.state = {
            top : []
        }
        this.loadHandler = this.loadHandler.bind(this);
        this.loadHandler();
    }
    loadHandler = () => {
        const payload = {
            "placement" : "Top Slide"
        }
        axios.post('http://13.229.249.101/dashboard/api/slide/view', payload)
        .then((response) => {
            this.setState({
                top : response.data
            });
            
            $('#nivoslider').nivoSlider({
                effect: 'random',
                slices: 15,
                boxCols: 10,
                boxRows: 10,
                animSpeed: 500,
                pauseTime: 5000,
                startSlide: 0,
                directionNav: true,
                controlNavThumbs: false,
                pauseOnHover: false,
                manualAdvance: false
             });

        })
        .catch((error) => {
            console.log(error);
        });  
    }
    render(){
        return(
            <div className="slider-area home-three-slider">
                <div className="container">
                    <div className="row">
                        <div className="col-md-12">
                            <div className="preview-2">
                                <div id="nivoslider" className="slides">
                                {
                                    this.state.top.map((slide,i)=>{
                                        const image = slide.placement.replace(" ","")+"-"+slide.id+".jpg";
                                        return <a href="#" key={i}><img src={"http://13.229.249.101/dashboard/api/web/uploads/slide/"+image} alt=""/></a>
                                    })
                                }	
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        );
    }
}

export default TopSlide;