import React, {Component} from 'react';
import $ from 'jquery';
import axios from 'axios';
import './Leftmenu.css';

class Leftmenu extends Component {
    constructor(props){
        super(props);
        this.state = {
            statuscollapse : '',
            brand : []
        }
        this.brandHandler = this.brandHandler.bind(this);
        this.brandHandler()
    }

    componentDidMount() {
        $(".sub-l-menu li a").click(function(){
            $(this).closest('li').find('.sb').slideToggle();
            $(this).toggleClass("active");
        });
        $(window).ready(function(){
            $(".sub-title").closest("li").find('.sb').slideUp(0);
            $(".sub-title.active").closest("li").find('.sb').slideDown();    
        });
    }

    brandHandler = () => {
        var headers = {
            'Content-Type': 'application/json'
          }
        axios.get('http://13.229.249.101/dashboard/api/brand/view/showonweb/', headers)
        .then((response) => {
            this.setState({
                brand : response.data
            })
        })
    }

    render(){
        return (
            <div className="col-md-3 left-menu">
                <h2 className="title_block">CATALOG</h2>
                <div className="col-12 my-3 px-0">
                    <ul className="sub-l-menu">
                        <li>
                            <a className="sub-title active">Brands <i className="fa fa-minus"></i><i className="fa fa-plus"></i></a>
                            <ul className="mt-2 px-2 sb">
                            {
                                this.state.brand.map((brand,i) => {
                                return <li className="py-1"><a href={'#/product/brands/'+brand.name}>{brand.name}</a></li>
                                })
                            }
                            </ul>
                        </li>
                        <li className="sub-l-menu">
                            <a className="sub-title">Apparel <i className="fa fa-minus"></i><i className="fa fa-plus"></i></a>
                            <ul className="mt-2 px-2 sb">
                                <li className="py-1"><a href="#/product/class/T-shirts">T-shirts</a></li>
                                <li className="py-1"><a href="#/product/class/Sport Shirts">Sport Shirts</a></li>
                                <li className="py-1"><a href="#/product/class/Fleece">Fleece</a></li>
                                <li className="py-1"><a href="#/product/class/Headwear">Headwear</a></li>
                            </ul>
                        </li>
                        <li className="sub-l-menu">
                            <a className="sub-title">Heat Transfer <i className="fa fa-minus"></i><i className="fa fa-plus"></i></a>
                            <ul className="mt-2 px-2 sb">
                                <li className="py-1"><a href="#/product/class/Polyflex">Polyflex</a></li>
                                <li className="py-1"><a href="#/product/class/Heat Press">Heat Press</a></li>
                            </ul>
                        </li>
                    </ul>
                </div>
            </div>
        );
    }
}

export default Leftmenu;