import React, {Component} from 'react';
import $ from 'jquery';
import './Leftmenu.css';

class Leftmenu extends Component {
    constructor(props){
        super(props);
        this.state = {
            statuscollapse : ''
        }
    }
    
    componentDidMount() {
        $(".sub-l-menu li a").click(function(){
            $(this).closest('li').find('.sb').slideToggle();
            $(this).toggleClass("active");
        });
        $(window).ready(function(){
            $(".sub-title").closest("li").find('.sb').slideUp(0);
            $(".sub-title.active").closest("li").find('.sb').slideDown();    
        });
    }

    render(){
        return (
            <div className="col-md-3 left-menu">
                <h2 className="title_block">MEMBER</h2>
                <div className="col-12 px-0">
                    <ul className="mt-3 sub-l-menu">
                        <li>
                            <a className="sub-title active">Account <i className="fa fa-minus"></i><i className="fa fa-plus"></i></a>
                            <ul className="mt-2 px-2 sb">
                                <li className="py-1"><a href="#/account/edit">Manage Account</a></li>
                                <li className="py-1"><a href="#/account/orderhistory">Order History</a></li>
                                <li className="py-1">Pending Order</li>
                                <li className="py-1"><a href="#/account/balancehistory">Balance History</a></li>
                                <li className="py-1">Report</li>
                            </ul>
                        </li>
                        <li className="sub-l-menu">
                            <a className="sub-title">Marketing Toolkit <i className="fa fa-minus"></i><i className="fa fa-plus"></i></a>
                            <ul className="mt-2 px-2 sb">
                                <li className="py-1">Catalog</li>
                                <li className="py-1">Photos</li>
                            </ul>
                        </li>
                        <li>
                            <a className="sub-title-black">Logout</a>
                        </li>
                    </ul>
                </div>
            </div>
        );
    }
}

export default Leftmenu;