import React, { Component } from 'react';
import {Route,Router,BrowserRouter,HashRouter} from "react-router-dom";
import axios from 'axios';
import {reactLocalStorage} from 'reactjs-localstorage';
import Layout from './components/Layout/Layout';
import FirstPage from './containers/FirstPage/FirstPage';
import DetailProducts from './containers/DetailProducts/DetailProducts';
import Accounts from './containers/Accounts/Accounts';
import Print from './containers/Print/Print';
import AuthContext from './context/auth-context';
import ProductList from './containers/ProductList/ProductList';
class App extends Component {
  constructor(props){
    super(props);
    this.state = {
      authenticated:false,
      load:false
    }
  }
  
  componentDidMount(){
    var token= reactLocalStorage.get('auth');
    if(this.state.load === false && token !== undefined){
      const AuthStr = "Bearer ".concat(token); 
      axios.get('http://13.229.249.101/dashboard/api/userapi/account/view', { headers: { Authorization: AuthStr } })
      .then(() => {
        this.setState({
          "authenticated": true,
          "load" : true
        });
      })
      .catch((error) => {
        this.setState({
          "authenticated": false,
          "load" : true
        });
      }); 
    } 
  }
  
  render() {  
    const paths = window.location.hash;
    return (
      <HashRouter>
        <div className="App">
        <AuthContext.Provider 
          value={{
            authenticated: this.state.authenticated
          }}>
          { paths.includes("/print") ?
            <Route path="/print" component={Print}/>
            :<Layout>
            <Route exact path="/" component={FirstPage}/>
            <Route path="/details-product/:idcategory" component={DetailProducts}/>
            <Route path="/account" component={Accounts}/>
            <Route path="/product/:type/:class" component={ProductList}/>
            </Layout>
          }
        </AuthContext.Provider>
        </div>
      </HashRouter>
    );
  }
}

export default App;
