import React,{Component} from 'react';
import Breadcrumb from '../../components/Breadcrumb/Breadcrumb';
import Leftmenu from '../../components/ProductList/Leftmenu/Leftmenu';
import ProductGrid from '../../components/ProductList/Rightcontent/ProductGrid';
class ProductList extends Component{
    constructor(props){
        super(props);
        this.state = {
            class : this.props.match.params.class,
            type : this.props.match.params.type
        }
        console.log(this.props.match.params.class+'-'+this.props.match.params.type);
    }
    componentDidUpdate(prevProps){
        if(this.props.match.params.class !== prevProps.match.params.class){
            this.setState({
                class : this.props.match.params.class,
                type : this.props.match.params.type
            })
        }
    }
    render(){
        return (
            <section>
            <Breadcrumb breadcrumb="Shop Grid View"></Breadcrumb>
            <div className="container">
                <div className="row">
                    <Leftmenu></Leftmenu>
                    <ProductGrid  classes={this.state.class} type={this.state.type}/>
                </div>
            </div>
            </section>
        );
    }
}

export default ProductList;