import React, {Component} from 'react';
import axios from 'axios';
import moment from 'moment';
import DayPickerInput from 'react-day-picker/DayPickerInput';
import { formatDate, parseDate } from 'react-day-picker/moment';
import 'react-day-picker/lib/style.css';
import {Link} from "react-router-dom";
import {reactLocalStorage} from 'reactjs-localstorage';
import "../Rightcontent/Rightcontent.css";
import NumberFormat from 'react-number-format';

  
class OrderHistory extends Component {
    constructor(props){
        super(props);
        this.state = {
            receiverName : "",
            receiverContact : "",
            receiverAddress : "",
            payment : "",
            total : "",
            courier : "",
            page : "",
            orderHistory : [],
            form: undefined,
            to: undefined,
            awal:undefined,
            akhir:undefined
        }
        this.timer = null;
        const payload = [{'key' : "page", 'operator' : "=", 'value' : 1}];
        var token = reactLocalStorage.get('auth');
        const AuthStr = "Bearer ".concat(token); 
        axios.post('http://13.229.249.101/dashboard/api/userapi/order/find', payload, { headers: { Authorization: AuthStr } })
        .then((response) => {
            this.setState({"orderHistory" : response.data});
        })
        .catch((error) => {
            this.setState({error: 'Periksa Kembali Data Pada From Edit Account'})
            console.log(error);
        });

    }

    showFromMonth() {
        const { from, to } = this.state;
        if (!from) {
          return;
        }
        if (moment(to).diff(moment(from), 'months') < 2) {
          this.to.getDayPicker().showMonth(from);
        }
    }

    handleFromChange = (from) => {
        var date = formatDate(from, "YYYY-MM-DD")+' '+'00:00:00';
        this.setState({ awal : date });
        this.setState({ from });
    }

    handleToChange = (to) => {
        var date = formatDate(to, "YYYY-MM-DD")+' '+'23:59:59';
        this.setState({ akhir : date });
        this.setState({ to }, this.showFromMonth);
        if(this.state.awal != undefined){
        this.handleCheck();
        }
    }

    componentDidUpdate (prevProps, prevState) {
        if((prevState.receiverName !== this.state.receiverName) || (prevState.receiverContact !== this.state.receiverContact) || (prevState.receiverAddress !== this.state.receiverAddress) || (prevState.payment !== this.state.payment) || (prevState.total !== this.state.total) || (prevState.courier !== this.state.courier) || (prevState.page !== this.state.page)) {
            this.handleCheck();
        }
    }

    handleCheck = () => {
        // Clears running timer and starts a new one each time the user types
        clearTimeout(this.timer);
        this.timer = setTimeout(() => {
            const data=[];
            if(this.state.awal !== '' && this.state.akhir !== ''){
                data.push({'key' : "addTime", 'operator' : "like", 'value': [this.state.awal,this.state.akhir]})
            }
            if(this.state.receiverName !== ''){
                data.push({'key' : "receiverName", 'operator' : "like", 'value' : this.state.receiverName})
            }
            if(this.state.receiverContact !== ''){
                data.push({'key' : "receiverContact", 'operator' : "like", 'value' : this.state.receiverContact})
            }
            if(this.state.receiverAddress !== ''){
                data.push({'key' : "receiverAddress", 'operator' : "like", 'value' : this.state.receiverAddress})
            }
            if(this.state.payment !== ''){
                data.push({'key' : "payment", 'operator' : "=", 'value' : this.state.payment})
            }
            if(this.state.total !== ''){
                data.push({'key' : "grandTotal", 'operator' : "=", 'value' : this.state.total})
            }
            if(this.state.courier !== ''){
                data.push({'key' : "shippingName", 'operator' : "like", 'value' : this.state.courier})
            }
            if(this.state.page !== ''){
                data.push({'key': "page", 'operator' : "=", 'value' : this.state.page})
            }
            else{
                data.push({'key': "page", 'operator' : "=", 'value' : 1})
            }

            const payload = data; 
            var token = reactLocalStorage.get('auth', true);
            const AuthStr = "Bearer ".concat(token); 
            axios.post('http://13.229.249.101/dashboard/api/userapi/order/find', payload, { headers: { Authorization: AuthStr } })
            .then((response) => {
                this.setState({"orderHistory" : response.data});
            })
            .catch((error) => {
                this.setState({error: 'Periksa Kembali Data Pada From Edit Account'})
                console.log(error);
            });
        }, 1000);
    }
    changeStateHandler = (e) => {
        let change = {}
        change[e.target.name] = e.target.value
        this.setState(change)
    }
    
    render (){
        const { from, to } = this.state;
        const modifiers = { start: from, end: to };
        return (
            <div className="col-md-9 mb-5 right-content">
                <div className="row">
                    <div className="col-12">
                        <h2 className="mb-2">Order History</h2>
                    </div>
                    <div className="col-12">
                    <p>Filter By:</p>
                    </div>
                    <div className="col-12">
                        <div className="row mx-0">
                            <div className="col-md-3 px-1 mb-2">
                            <input type="text" className="form-control" name="receiverName" value={this.state.receiverName} onChange={this.changeStateHandler} placeholder="Receiver Name"></input>
                            </div>
                            <div className="col-md-3 px-1 mb-2">
                                <input type="text" className="form-control" name="receiverContact" value={this.state.receiverContact} onChange={this.changeStateHandler} placeholder="Receiver Contact"></input>
                            </div>
                            <div className="col-md-3 px-1 mb-2">
                                <input type="text" className="form-control" name="receiverAddress" value={this.state.receiverAddress} onChange={this.changeStateHandler} placeholder="Receiver Address"></input>
                            </div>
                            <div className="col-md-3 px-1 mb-2">
                                <select name="payment" className="form-control" onChange={this.changeStateHandler} >
                                    <option value="">-Select-</option>
                                    <option value="Yes">Yes</option>
                                    <option value="No">No</option>
                                </select>
                            </div>
                            <div className="col-md-3 px-1 mb-2">
                                <input type="text" className="form-control" name="total" value={this.state.total} onChange={this.changeStateHandler} placeholder="Total"></input>
                            </div>
                            <div className="col-md-3 px-1 mb-2">
                                <input type="text" className="form-control" name="courier" value={this.state.courier} onChange={this.changeStateHandler} placeholder="Courier"></input>
                            </div>
                            <div className="col-md-3 px-1 mb-2">
                                <label className="float-left pt-2 pr-1 mb-0">Page</label> <input name="page" type="number" value={this.state.page} onChange={this.changeStateHandler}  className="form-control mw-100" placeholder="1"></input>
                            </div>
                            <div className="w-100"></div>
                            <div className="col-md-3 px-1 mb-2">
                                <div className="InputFromTo">
                                    <DayPickerInput
                                    value={from}
                                    placeholder="From"
                                    format="YYYY-MM-DD"
                                    formatDate={formatDate}
                                    parseDate={parseDate}
                                    dayPickerProps={{
                                        selectedDays: [from, { from, to }],
                                        disabledDays: { after: to },
                                        toMonth: to,
                                        modifiers,
                                        numberOfMonths: 2,
                                        onDayClick: () => this.to.getInput().focus()
                                    }}
                                    onDayChange={this.handleFromChange}
                                    />{' '}
                                    {' '}  
                                </div>
                            </div>
                            <div className="col-md-3 px-1 mb-2">
                                <span className="InputFromTo-to">
                                <DayPickerInput
                                    ref={el => (this.to = el)}
                                    value={to}
                                    placeholder="To"
                                    format="YYYY-MM-DD"
                                    formatDate={formatDate}
                                    parseDate={parseDate}
                                    dayPickerProps={{
                                    selectedDays: [from, { from, to }],
                                    disabledDays: { before: from },
                                    modifiers,
                                    month: from,
                                    fromMonth: from,
                                    numberOfMonths: 2,
                                    }}
                                    onDayChange={this.handleToChange}
                                />
                                </span>
                            </div>
                        </div>
                    </div>
                </div>
                <div className="row mx-0 mt-3 py-2 border-top border-bottom">
                    <div className="col-lg-2"><strong>#</strong></div>
                    <div className="col-lg-8"><strong>Detail</strong></div>
                    <div className="col-lg-2"><strong>Grand Total</strong></div>
                </div>
                {
                    this.state.orderHistory.map((history,i) => {
                        return(
                        <div className="row mx-0 py-2 border-bottom" key={i}>
                            <div className="col-lg-2">
                                <div className="mb-3"><strong>{history.id}</strong></div>
                                <Link to={process.env.PUBLIC_URL+"/print/order/"+history.id} target="_blank">
                                   <p className="t-default font-weight-bold"><i className="fa fa-print"></i> Print</p>
                                </Link>
                                <Link><p className="t-default font-weight-bold"><i className="fa fa-map"></i> Track</p></Link>
                            </div>
                            <div className="col-lg-8">
                                <p>{history.addTime}<br/>Location : {history.locationName}</p>
                                <p>Total Quantity: {history.totalQuantity} pcs</p>
                                <hr/>
                                <p>Sender:<br/>{history.senderName} ({history.senderContact})</p>
                                <p>Receiver:<br/>
                                {history.receiverName} ({history.receiverContact})<br/>
                                {history.receiverAddress}
                                </p>
                                <p>
                                    {history.shippingName} ({history.shippingPrice})
                                </p>
                                {
                                history.trackingNumber !== '' ?
                                <p>No Resi : {history.trackingNumber}</p>
                                :null
                                }
                            </div>
                            <div className="col-lg-2"><NumberFormat value={history.grandTotal} displayType={'text'} thousandSeparator={'.'} decimalSeparator={','} prefix={''} /><p className="pt-3">Payment<br/>{history.payment === 'Yes' ? <span><i className="fa fa-check"></i> Done</span> : <span><i className="fa fa-warning"></i> No</span> }</p></div>
                        </div>
                        );
                    })
                }
            </div>
        );
    }
}

export default OrderHistory;