import React,{Component} from 'react';
import axios from 'axios';

class BottomSlide extends Component{
    constructor(props){
        super(props);
        this.state = {
            bottom : []
        }
        this.loadHandler = this.loadHandler.bind(this);
        this.loadHandler();
    }
    loadHandler = () => {
        const payload = {
            "placement" : "Bottom Slide"
        }
        axios.post('http://13.229.249.101/dashboard/api/slide/view', payload)
        .then((response) => {
            this.setState({
                bottom : response.data
            })
        })
        .catch((error) => {
            console.log(error);
        });  
    }
    render(){
        return(
            <div className="home-three-banner-area">
                <div className="container">
                    <div className="row">
                    {
                        this.state.bottom.slice(0,3).map((slide,i)=>{
                        const image = slide.placement.replace(" ","")+"-"+slide.id+".jpg";
                        return <div className="col-lg-4 col-md-4 col-sm-6 col-xs-12" key={i}>
                            <a href="#" className="white-hover">
                                <img src={"http://13.229.249.101/dashboard/api/web/uploads/slide/"+image} alt=""/>
                            </a>
                        </div>
                        })
                    }
                    </div>
                </div>
            </div>
        );
    }
}

export default BottomSlide;