import React, {Component} from 'react';
import axios from 'axios';
import {reactLocalStorage} from 'reactjs-localstorage';
import './PrintOrder.css';
class PrintOrder extends Component {
    constructor(props){
        super(props);
        this.state = {
            print : []
        }
        
    }
    componentDidMount(){
        let par = this.props.match.params.orderid;
        this.loadPrintOrder(par);
    }
   
    loadPrintOrder = (id) => {
        var token= reactLocalStorage.get('auth');
        const AuthStr = "Bearer ".concat(token); 
        axios.get('http://13.229.249.101/dashboard/api/userapi/order/view/'+id , { headers: { Authorization: AuthStr } })
        .then((response) => {
            this.setState({"print" : response.data});
        })
        .catch((error) => {
            document.location.href="";
            this.setState({error: 'Periksa Kembali Data Pada From Edit Account'})
            console.log(error);
        });
    }

    render(){
        const {print} = this.state;          
        return(
            <div className="order-print">
                <div className="row">
                    <div className="col-12">
                    <div className="d-inline-block">
                        <h2>CITITEX HQ</h2>
                        <h3>INVOICE</h3>
                    </div>
                    <div className="d-inline-block">
                        <table className="header__detail-table">
                            <tbody>
                            <tr >
                                <td >Order#</td> 
                                <td >: {print.id}</td>
                            </tr> 
                            <tr >
                                <td >Date</td> 
                                <td >: {print.addTime}</td>
                            </tr> 
                            <tr >
                                <td >Submit</td> 
                                <td >: {print.submit}</td>
                            </tr> 
                            <tr >
                                <td >Location</td> 
                                <td >: {print.locationName}</td>
                            </tr>
                            </tbody>
                        </table>
                    </div>
                </div>
                    <div className="col-6 py-4">
                        <div  className="address__body">
                            <div  className="address__body-name">Ship To:</div> 
                            <div  className="address__body-address">
                                {print.receiverName} ({print.receiverContact})<br />
                                {print.receiverAddress} {print.postalCode}
                            </div>
                        </div>
                    </div>
                    <div className="col-12">
                        <table cellSpacing="10px" className="list__table">
                            <thead>
                                <tr>
                                    <th width="60">QTY</th>
                                    <th>DESCRIPTION</th>
                                    <th width="130">UNIT PRICE</th>
                                    <th width="120">AMOUNT</th>
                                </tr>
                            </thead>
                            <tbody>
                            { 
                            print !== '' ?
                            print.orderItem.map((orders,i) => {
                                return(
                                    <tr key={i}>
                                        <td colSpan="4" >
                                            { 
                                            orders.mediaItem === undefined || orders.mediaItem === null ? 
                                            <table className="table__project">
                                            <tbody>
                                                <tr>
                                                    <td width="60" className="pl-2">{orders.quantity}</td>
                                                    <td>{orders.categoryName+' '+orders.productDescription}</td>
                                                    <td width="130">{orders.price !== undefined ? orders.price.toLocaleString() : 0}</td>
                                                    <td width="120">{orders.price !== undefined ? (orders.price * orders.quantity).toLocaleString() : 0}</td>
                                                </tr>
                                            </tbody>
                                            </table>:
                                            
                                            <div className="col-12">
                                            <table className="list__table__project">
                                            <tbody>
                                            {
                                            orders.mediaItem.map((media,i) => {
                                                return [
                                                <tr key={i}>
                                                    <td colSpan="4" className="px-2">
                                                        <div className="mb-2"><img alt="" src="http://54.169.12.131/api/web/uploads/orderproject/10075.jpeg" className="img-fluid w-100"/></div>
                                                    </td>
                                                </tr>,
                                                <tr>
                                                    <td width="60" className="pl-2">{media.quantity}</td>
                                                    <td>{media.categoryName+' '+media.productDescription}</td>
                                                    <td width="130">{media.productPrice.toLocaleString()}</td>
                                                    <td width="120">{(media.productPrice * media.quantity).toLocaleString()}</td>
                                                </tr>
                                                ]
                                            })
                                            }
                                            </tbody>
                                            </table>
                                            <table width="100%">
                                            <tbody>
                                            {
                                            orders.workOrder.map((work,i) => {
                                                return( 
                                                    <tr key={i}>
                                                        <td width="60"></td>
                                                        <td className="list__table-workorder">
                                                            <div className="list__table-workorder__desc">
                                                                {work.sideName+'->'+work.workTypeName+'->'+work.width+' cm x '+work.height+'cm'}
                                                            </div>
                                                        </td>
                                                        <td width="130">{work.workTypePrice.toLocaleString()}</td>
                                                        <td width="120">{work.workTypePrice.toLocaleString()}</td>
                                                    </tr>
                                                )
                                            })
                                            }
                                            </tbody>
                                            </table>
                                            </div>
                                            
                                            }
                                            
                                        </td>
                                    </tr> 
                                )
                            })
                            :null
                            }
                                <tr>
                                    <td colSpan="4" >
                                        <table className="list__table__separate"></table>
                                    </td>
                                </tr>
                            </tbody>
                            <tfoot>
                                <tr>
                                    <td colSpan="4">Total Quantity: {print.totalQuantity}</td>
                                </tr>
                                <tr>
                                    <td colSpan="2" rowSpan="3" className="list__table__total-quantity">
                                        <div className="list__table__total-quantity__box">
                                        {print.note}
                                        </div>
                                    </td>
                                    <td className="list__table__total-price">
                                        TOTAL
                                    </td>
                                    <td>
                                    {
                                    print.shippingPrice !== null?
                                    (print.grandTotal - (print.shippingPrice * print.shippingKg)).toLocaleString()
                                    :null
                                    }
                                    </td>
                                </tr>
                                <tr>
                                    <td className="list__table__total-price">
                                        SHIPPING
                                    </td>
                                    <td>
                                        {parseInt(print.totalShipping).toLocaleString()}
                                    </td>
                                </tr>
                                <tr>
                                    <td className="list__table__total-price">
                                        GRAND TOTAL
                                    </td>
                                    <td>
                                        {parseInt(print.grandTotal).toLocaleString()}
                                    </td>
                                </tr>
                            </tfoot>
                        </table>
                    </div>
                </div> 
            </div>     
        )
    }
}

export default PrintOrder;